@charset "utf-8";

.wrap {
  width: 1200px;
  margin: 50px auto;
  text-align: center;
}

.login {
  color: #111;
  font-size: 20px;
}

.input {
  width: 350px;
  height: 50px;
  padding: 0px 11px 1px 15px;
  border-radius: 4px;
  border: 1px solid rgb(221, 221, 221);
  font-weight: 400;
  font-size: 14px;
  line-height: 1.5;
  color: rgb(51, 51, 51);
  outline: none;
  box-sizing: border-box;
  display: block;
  margin: 10px auto;
}

.find-wrap {
  width: 350px;
  margin: 0px auto;
  margin-bottom: 20px;
  text-align: right;
}

a {
  color: #333;
  text-decoration: none;
  position: relative;
  font-size: 14px;
}

a:first-of-type {
  margin-right: 20px;
}

a:first-of-type::after {
  content: '';
  width: 1px; height: 15px;
  display: block;
  background: #888;
  position: absolute;
  right: -10px; top: 2px;
}

.login-btn {
  width: 350px;
  height: 54px;
  display: block;
  margin: 0px auto;
  padding: 0px 10px;
  font-size: 16px;
  text-align: center;
  overflow: hidden;
  border-radius: 3px;
  color: rgb(255, 255, 255);
  background-color: rgb(95, 0, 128);
  border: 0px none;
  cursor: pointer;
}

.join-btn {
  width: 350px;
  height: 54px;
  display: block;
  margin: 0px auto;
  margin-top: 10px;
  padding: 0px 10px;
  font-size: 16px;
  text-align: center;
  overflow: hidden;
  border-radius: 3px;
  color: rgb(95, 0, 128);
  background-color: rgb(255, 255, 255);
  border: 1px solid rgb(95, 0, 128);
  cursor: pointer;
}