@charset "UTF-8";

@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@400;500&display=swap');
*{font-family: 'Noto Sans KR', sans-serif;}

ul li {list-style:none;}
a {text-decoration:none; color: inherit;}

.k-title {
  text-align: center;
  font-size: 32px;
  font-weight: 500;
  margin: 20px 0;
}

.k-ul {
  width: 1200px;
  display: flex;
  justify-content: space-between;
  gap: 20px;
  margin: 0 auto;
  margin-bottom: 40px;
}

.k-li {
  width: 100%;
  position: relative;
}

.k-li p img:hover{
  width: 105%;
}

.k-li p img {
  width: 100%;
  transition: .5s;
  cursor: pointer;
}

.k-li p:first-child {
  width: 100%; height: 360px;
  overflow: hidden;
}

.k-li p:nth-child(3) {
  font-weight: bold;
}

.k-li p:nth-child(4) {
  font-size: 12px;
  color: #888;
}

.cart {
  width: 45px; height: 45px;
  position: absolute;
  bottom: 145px; right: 15px;
}